.center-xy {
  height: calc( 100vh - 57px );
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

#main {
  .Group {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 16px;
    line-height: 20px;
    color: #818C99;
    .Button {
      margin-top: 24px;
      .Button__content {
        font-size: 15px;
        line-height: 20px; } } } }

// ТИП СБОРА

#type-collection {
  .Group {
    width: 100%;
    .Card {
      width: 100%;
      .Icon--target_outline_28 {
        svg {
          color: #4986cc; } }
      .Icon--chevron_right_outline_28 {
        svg {
          color: #B8C1CC; } }
      .Button {
        margin-top: 14px;
        .Button__content {
          font-size: 15px;
          line-height: 20px; } } } } }

//

#collection-form {
  .Button--lvl-outline {
    border-style: dashed; }
  label.Tappable {
    padding: 40px 0; } }

//

#create-post {
  .PanelHeader__left {
    padding-left: 16px;
    .Icon {
      width: 28px !important;
      height: 28px !important;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      background-color: #F0F1F3;
      svg {
        color: #818C99; } } }
  .PanelHeader__right {
    padding-right: 16px;
    .Icon {
      height: 28px;
      width: 28px;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #3F8AE0;
      border-radius: 50%;
      svg {
        height: 22px;
        width: 22px;
        color: #fff; } } }
  .PanelHeader__content {
    display: flex;
    align-items: center;
    svg {
      color: #3F8AE0; } } }
